/* ======= Start Of Secondary Navigation section ======= */
.contact-nav{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 0 2rem;
    background-color: var(--default-light);
}

.social-icons {
    padding: .7rem .7rem;
    font-size: 1.1rem;
    color: var(--gray-dark);
    transition: all .3s ease-out;
    cursor: pointer;
}

.social-icons:hover{
    color: var(--secondary-hover);
    transform: scale(1.5);
}

.contact-mail {
    display: flex;
    margin-left: auto;
    padding-right: 1.3rem;
    align-items: center;
}

.contact-mail span{
    font-size: .7rem;
    opacity: .5;
    letter-spacing: 1px;
    font-weight: bold;
    transition: all .3s ease-out;
    cursor: pointer;
}

.contact-mail span:hover{
    transform: scale(1.1);
}

.contact-mail span:first-of-type{
    padding-right: 2rem;
}

/* ======= End Of Secondary Navigation section ======= */

/* ======= Start Of Primary Navigation section ======= */
.nav{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2rem;
    box-shadow: var(--shadow-bottom-light);
}

.nav .logo{
    padding-left: 1rem;
    width: 120px;
}

.nav .right-icons-container {
    display: flex;
    align-items: center;
    padding-right: 1.3rem;
}

/* primary navigation icon section */
.right-icons-container .icons{
    padding: .5rem;
    background: var(--default-light);
    color: var(--primary);
    font-size: 1.5rem;
    border-radius: var(--circle);
    transition: all .4s ease-out;
    cursor: pointer;
    overflow: hidden;
    margin-right: 1.2rem;
}

.right-icons-container .icons:hover{
    background: var(--primary);
    color: white;
}

/* primary navigation links section */
.nav .ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding-inline-start: 0;
}

.nav .ul > li{
    transition: .3s ease-out;
    position: relative;
}

.nav .ul > li a{
    padding: 1.7rem 1rem;
    text-decoration: none;
    color: var(--primary);
    text-transform: uppercase;
    position: relative;
    display: block;
    transition: all .6s ease-in-out;
    font-size: .75rem;
    font-family: 'Nunito', sans-serif;
    letter-spacing: 1px;
    font-weight: bold;
}

.nav .ul > li a:active::after{
    width: 100%;
    height: 5px;
    background: white;
}

.nav .ul > li a:hover{
    color: aliceblue;
    z-index: 1;
}

.nav .ul > li a::after{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: left;
    content: '';
    margin: 0;
    opacity: 0;
    z-index: -1;
}

.nav .ul > li a:hover::after{
    /* z-index: -10; */
    animation: fill .6s forwards;
    -webkit-animation: fill .6s forwards;
    -moz-animation: fill .6s forwards;
    opacity: 1;
}
/* header navigation section ends here */

/* Primary Navigation Keyframes */
@keyframes fill {
    0% {
      height: 0%;
      width: 1px;
    }
    50% {
      height: 100%;
      width: 1px;
    }
    100% {
      height: 100%;
      width: 100%;
      background: var(--primary);
    }
}

/* ======= End Of Primary Navigation section ======= */