@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ======= Start Of Secondary Navigation section ======= */
.contact-nav{
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 0 2rem;
    background-color: var(--default-light);
}

.social-icons {
    padding: .7rem .7rem;
    font-size: 1.1rem;
    color: var(--gray-dark);
    transition: all .3s ease-out;
    cursor: pointer;
}

.social-icons:hover{
    color: var(--secondary-hover);
    transform: scale(1.5);
}

.contact-mail {
    display: flex;
    margin-left: auto;
    padding-right: 1.3rem;
    align-items: center;
}

.contact-mail span{
    font-size: .7rem;
    opacity: .5;
    letter-spacing: 1px;
    font-weight: bold;
    transition: all .3s ease-out;
    cursor: pointer;
}

.contact-mail span:hover{
    transform: scale(1.1);
}

.contact-mail span:first-of-type{
    padding-right: 2rem;
}

/* ======= End Of Secondary Navigation section ======= */

/* ======= Start Of Primary Navigation section ======= */
.nav{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 2rem;
    box-shadow: var(--shadow-bottom-light);
}

.nav .logo{
    padding-left: 1rem;
    width: 120px;
}

.nav .right-icons-container {
    display: flex;
    align-items: center;
    padding-right: 1.3rem;
}

/* primary navigation icon section */
.right-icons-container .icons{
    padding: .5rem;
    background: var(--default-light);
    color: var(--primary);
    font-size: 1.5rem;
    border-radius: var(--circle);
    transition: all .4s ease-out;
    cursor: pointer;
    overflow: hidden;
    margin-right: 1.2rem;
}

.right-icons-container .icons:hover{
    background: var(--primary);
    color: white;
}

/* primary navigation links section */
.nav .ul{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
}

.nav .ul > li{
    transition: .3s ease-out;
    position: relative;
}

.nav .ul > li a{
    padding: 1.7rem 1rem;
    text-decoration: none;
    color: var(--primary);
    text-transform: uppercase;
    position: relative;
    display: block;
    transition: all .6s ease-in-out;
    font-size: .75rem;
    font-family: 'Nunito', sans-serif;
    letter-spacing: 1px;
    font-weight: bold;
}

.nav .ul > li a:active::after{
    width: 100%;
    height: 5px;
    background: white;
}

.nav .ul > li a:hover{
    color: aliceblue;
    z-index: 1;
}

.nav .ul > li a::after{
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: left;
    content: '';
    margin: 0;
    opacity: 0;
    z-index: -1;
}

.nav .ul > li a:hover::after{
    /* z-index: -10; */
    animation: fill .6s forwards;
    -webkit-animation: fill .6s forwards;
    -moz-animation: fill .6s forwards;
    opacity: 1;
}
/* header navigation section ends here */

/* Primary Navigation Keyframes */
@keyframes fill {
    0% {
      height: 0%;
      width: 1px;
    }
    50% {
      height: 100%;
      width: 1px;
    }
    100% {
      height: 100%;
      width: 100%;
      background: var(--primary);
    }
}

/* ======= End Of Primary Navigation section ======= */
/* this is the styling for the fixed floating post ads button */
.add-post-btn{
    padding: .5rem;
    background: var(--default-light);
    color: var(--primary);
    font-size: 2rem;
    border-radius: var(--circle);
    transition: all .4s ease-out;
    cursor: pointer;
    overflow: hidden;
    margin-right: 1.2rem;
    position: fixed;
    bottom: 50px;
    right: 50px;
}

.add-post-btn:hover{
    background: var(--secondary-hover);
    color: white;
    transform: scale(1.2);
}


/* this is the banner of the landing page styling. both the left and right */
.banner, .banner > *{
    box-sizing: border-box;
}

.banner{
    width: 100%;
    height: 85vh;
    background: #f7f6f6;
    padding: 2rem;
    font-family: 'Nunito', sans-serif;
}

.banner-inner{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.sides{
    height: 100%;
    width: 100%;
    padding: 2rem;
}

.left{
    display: flex;
    align-items: center;
}

.left .intro{
    width: 100%;
}

.left .intro h1{
    margin-top: 0;
    opacity: .8;
    font-weight: bolder;
    color: var(--primary);
}

.left .intro h2{
    opacity: .5;
}

.search-label{
    display: flex;
    justify-content: space-between;
    margin-bottom: .5rem;
    opacity: .4;
    font-weight: bold;
}

.call-to-action{
    margin-top: 3rem;
    display: flex;
    align-items: center;
}

.call-to-action .action-btn{
    font-size: .8rem;
    letter-spacing: 1px;
}

.call-to-action .action-btn:first-of-type{
    margin-right: 1rem;
}

.right{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 0;
}

.right img{
    max-height: 100%;
    max-width: 100%;
}

/* this is the styling for the section heading component */
.section-heading{
    color: white;
    text-transform: uppercase;
    position: relative;
    display: inline;
    font-weight: bold;
    font-size: 1.5rem;
    align-items: center;
    animation: move-after .7 linear infinite;
    margin-left: 1rem;
}

.section-heading::before, .section-heading::after{
    position: absolute;
    content: "";
    height: 7px;
    width: 30px;
    bottom: -15px;
}

.section-heading::after{
    background: var(--secondary-hover);
    left: 1px;
    border-radius: 2px 0 0 2px;
    animation: move-after 1s linear infinite;
}

.section-heading::before{
    background: white;
    left: 31px;
    border-radius: 0 2px 2px 0;
    animation: move-before 1s linear infinite;
}

@keyframes move-after{
    0%{
        left: 1px;
    }
    50%{
        left: 31px;
        border-radius: 2px;
    }
    100%{
        left: 1px;
    }
}

@keyframes move-before{
    0%{
        left: 31px;
    }
    50%{
        left: 1px;
        border-radius: 2px;
    }
    100%{
        left: 31px;
    }
}
html, body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  /* main color sections */
  --primary: #3f51b5;
  --primary-hover: #1976d2;
  --secondary: #c51162;
  --secondary-hover: #f50057;
  --default-light: #f5f5f5;
  --default-light-variant: #cecece;
  --default-dark: #061826;
  --default-dark-variant: #595e61;

  /* color variation */
  --gray-primary: #D3D3D3;
  --gray-silver: #C0C0C0;
  --gray-dark: #A9A9A9;

  /* Typography color section */
  --tags: #36827F;
  --tags2: #9B287B;
  --text-primary: #424342;

  /* shadows color section */
  --light-shadow: #bbbbbb;
  --shadow-bottom-light: 3px 3px 3px var(--light-shadow);

  /* Edges shaping section */
  --circle: 50px;
}

.ripple:hover{
  animation: at-ripple .7s linear infinite;
  -webkit-animation: at-ripple .7s linear infinite;
  -moz-animation: at-ripple .7s linear infinite;
}

.ripple-always{
  animation: at-ripple .7s linear infinite;
  -webkit-animation: at-ripple .7s linear infinite;
  -moz-animation: at-ripple .7s linear infinite;
}

@keyframes at-ripple {
  0% {
    box-shadow: 0 4px 10px rgba(102, 102, 102, 0.1), 0 0 0 0 rgba(102, 102, 102, 0.1), 0 0 0 5px rgba(102, 102, 102, 0.1), 0 0 0 10px rgba(102, 102, 102, 0.1);
  }
  100% {
    box-shadow: 0 4px 10px rgba(102, 102, 102, 0.1), 0 0 0 5px rgba(102, 102, 102, 0.1), 0 0 0 10px rgba(102, 102, 102, 0.1), 0 0 0 20px rgba(102, 102, 102, 0);
  }
}

